
import AppMineRegister from "../register/appMineRegister"
import Toast from 'vux/src/components/toast/index.vue'

import { mapState, mapMutations } from "vuex"
import axios from "axios"
import {getAliKey} from "../../chat/api/getAliKey"
import {getMyId} from "../../chat/api/myId"


export default {
name: "app-mine-login-box",
data() {
return {
userInfo: {
userName: "",
password: ""
}
}
},
methods: {

userInfos(e, params) {
let that = this;
axios
.post("http://api.zhimaim.com/api/token", {
username: this.userInfo.userName,
password: this.userInfo.password
})
.then(res => {


if (res.data.success === true) {

window.localStorage.setItem("userName", this.userInfo.userName);
window.localStorage.setItem("token_expire",res.data.data.expires_in);
window.localStorage.setItem("token", res.data.data.access_token);
getAliKey({userName: this.userInfo.userName})
getMyId({userName: this.userInfo.userName})

this.$vux.toast.show({
text: '登录成功！'
})
this.$router.push({ path: "/message" })
} else {
this.$vux.toast.show({
type: 'cancel',
text: '用户名或密码错误'
})
}
})
.catch(res => {
this.$vux.toast.show({
type: 'warn',
text: '网络故障！'
})
throw res;
})
}
},
components: {
AppMineRegister
},
computed: {
...mapState(["userMsg"])
}
};



