import axios from 'axios'
export function getMyId (params) {
  axios
    .post('http://121.42.177.97:8080/red/user/findfriend.do?name=' + params.userName)
    .then(res => {
      console.log(res.data.data)
      window.localStorage.setItem('myId', res.data.data.user_id)
    })
    .catch(res => {
      throw res
    })
}



