import axios from 'axios'
// import {aliLogin} from './aliLogin'
// import {aliListener} from './aliListener'
export function getAliKey (params) {
  axios
    .post('http://121.42.177.97:8080/redfive/im/user.do?user_name=' + params.userName)
    .then(res => {
      console.log(res.data)
      window.localStorage.setItem('uid', res.data.data.im_user)
      window.localStorage.setItem('appkey', res.data.data.appkey)
      window.localStorage.setItem('credential', res.data.data.im_pwd)
      // aliLogin()
      // aliListener()
    })
    .catch(res => {
      throw res
    })
}



