import axios from 'axios'
import TopHeader from 'components/common/topHeader'
import AppMineLogin from './login/appMineLogin'
import AppMineRegister from './register/appMineRegister'
// import AppMinePersonal from './personal/AppMinePersonal'
const name = window.localStorage.getItem("userName")
export default {
	name: 'app-mine',
	data() {
		return {
			personal: [],
			dtsl: "",
			wbsl: "",
			rmsl: "",
			hbsl: "",
			name,
			hasPic: false
		}
	},
	components: {
		TopHeader,
		AppMineLogin,
		AppMineRegister
	},
	mounted() {
		if(this.name !== null) {
			axios
				.get("http://121.42.177.97:8080/red/use/pic_id.do?user_id=" + this.name)
				.then(res => {
					this.hasPic = true
				})
				.catch(res => {
					throw res
				})
		}
		//获得个人昵称
		axios.post('http://121.42.177.97:8080/red/user/findfriend.do?name=' + this.name)
			.then((response) => {
				this.personal = response.data.data
				// 查询动态个数
				var self = this
				axios.get('http://121.42.177.97:8080/redfive/face/count_circle.do', {
							params: {
								user_name: self.personal.user_name
							}
						}

					)
					.then((response) => {
						//					console.log(response)
						this.dtsl = response.data.data
						//					this.personal = response.data.data
					})
					.catch(function(error) {
						console.log(error);
					});
				// 查询微博个数
				axios.get('http://121.42.177.97:8080/redfive/blog/count_circle.do', {
						params: {
							user_name: self.personal.user_name
						}
					})
					.then((response) => {
						//					console.log(response)
						this.wbsl = response.data.data
					})
					.catch(function(error) {
						console.log(error);
					});
				// 查询好友个数	
				axios.get('http://121.42.177.97:8080/red/user/count_friend.do', {
						params: {
							user_id: self.personal.user_id
						}
					})
					.then((response) => {
						//					console.log(response)
						this.rmsl = response.data.data
					})
					.catch(function(error) {
						console.log(error);
					});
				// 查询红包个数
				axios.get('http://121.42.177.97:8080/red/payred/pay_count.do', {
						params: {
							user_id: self.personal.user_id
						}
					})
					.then((response) => {
						//					console.log(response)
						this.hbsl = response.data.data
					})
					.catch(function(error) {
						console.log(error);
					});
			})
			.catch(function(error) {
				console.log(error);
			});

	}
}



